import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Backdrop from '@mui/material/Backdrop';

const dispatchedEvents = [];

const SrsMuiBackdrop = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <Backdrop {...componentProps}>
      <slot />
    </Backdrop>
  );
}

const styles = `
  /* Custom styles for the Backdrop component */
`;

const props = [
  {
    name: "open",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "invisible",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "transitionDuration",
    defaultValue: 300,
    type: "number",
  },
  {
    name: "timeout",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "onMouseDown",
    defaultValue: null,
    type: "function",
  },
  {
    name: "onMouseUp",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Backdrop if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiBackdrop), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
